import { Badge } from "@material-ui/core";
import React from "react";
import { img_300 } from "../../config/config";
import ContentModal from "../ContentModal/ContentModal";
import './SingleContent.css'

const SingleContent = ({
  id,
  poster,
  title,
  date,
  media_type,
  vote_average,
}) => {
  return <ContentModal media_type={media_type} id={id}>
    <Badge badgeContent={vote_average} color={vote_average > 6 ? "primary" : "secondary"} overlap="rectangular"/>
   <img className="poster" src={ poster ? `${img_300}/${poster}` : <h2>Unavailable</h2> } alt={title}/>
   <b className="title">{title}</b>
   <span className="subTitle">{media_type === 'tv' ? "TV Series" : "Movie"}
   <span className="subTitle">{date}</span>
   
   </span>
  </ContentModal>;
};

export default SingleContent;
